<template>
  <div
      class="movie"
      v-show="!loading && !errorMessage"
  >
    <div class="movie-thumb">
      <img
          :alt="`The movie titled: ${title}`"
          :src="poster"
      >
    </div>
    <h3>{{ title }} ({{ year }})</h3>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  name: "Movie",
  props: {
    title: null,
    year: null,
    posterSrc: null
  },
  data() {
    const DEFAULT_PLACEHOLDER_IMAGE = 'data:image/png;base64,'
    return {
      poster: this.posterSrc === 'N/A' ? DEFAULT_PLACEHOLDER_IMAGE : this.posterSrc,
    }
  },
  computed: {
    ...mapGetters(['loading', 'errorMessage'])
  }
}
</script>

